___CSS_LOADER_EXPORT___.push([module.id, `html, body, #root {
    height: 100%;
}

body {
    font-family: sans-serif;
    color: #666;
    margin: 0;
}

#root {
    display: flex;
    flex-direction: column;
}
#root > div.graph {
    flex: 1;
    overflow: auto;
    position: relative;
}

.toolbar {
    flex-grow: 0;
    padding: 10px 20px;
    background: ghostwhite;
    display: flex;
    justify-content: space-between;
}
.toolbar * {
    vertical-align: text-bottom;
}

button {
    border: none;
    background: linear-gradient(to bottom, #abb8db, #8f9fc9);
    border-radius: 3px;
    padding: 3px 7px;
    color: #fff;
    outline: none;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, .2);
    margin-right: 5px;
}
button.grp {
    margin-right: 15px;
}
button:active {
    background: #6a7db0;
}
button.action {
    background: linear-gradient(to bottom, #ee9564, #de7d48);
}
button.action:active {
    background: #d67540;
}
button.action:disabled {
    background: #c1c1c1;
}

#root > div > svg {
    position: absolute;
    user-select: none;
}


.node.selected .nodeBorder, .edge.selected path, .edge.selected rect {
    stroke: #29c229;
}
.edge .v-dot {
    fill: transparent;
    stroke: none;
    stroke-width: 3px;
}
.edge .v-dot.selected {
    stroke: #29c229;
}
.edge .v-dot.auto.selected {
    stroke: #777;
}
circle#prj {
    fill: none;
    stroke: #777;
}

.nodeShadow {
    fill: none;
    stroke-width: 4px;
    stroke: rgba(0, 0, 0, 0.13);
}

g.node {
    user-select: none;
    cursor: default;
}

g.node text {
    pointer-events: none;
}

.icon {
    fill: #aaa;
    stroke: #fff;
}
#icon-cube {
    fill: #ee974b;
}

rect.elastic {
    pointer-events: none;
    stroke: none;
    fill: #3bd8281f;
    display: none;
}
rect.elastic.on {
    display: block;
}

.popover {
    position: absolute;
    top: 50px;
    bottom: 10px;
    overflow: auto;
    right: 10px;
    background: ghostwhite;
    padding: 30px;
    box-shadow: 3px 3px 5px rgba(0,0,0, .2);
    border: solid 1px #eee;
}

.popover th {
    text-align: left;
    padding: 20px 0px 10px;
}
.popover td {
    padding-right: 20px;
    font-size: 14px;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,YAAY;AAChB;;AAEA;IACI,uBAAuB;IACvB,WAAW;IACX,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;AACA;IACI,OAAO;IACP,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,sBAAsB;IACtB,aAAa;IACb,8BAA8B;AAClC;AACA;IACI,2BAA2B;AAC/B;;AAEA;IACI,YAAY;IACZ,wDAAwD;IACxD,kBAAkB;IAClB,gBAAgB;IAChB,WAAW;IACX,aAAa;IACb,yCAAyC;IACzC,iBAAiB;AACrB;AACA;IACI,kBAAkB;AACtB;AACA;IACI,mBAAmB;AACvB;AACA;IACI,wDAAwD;AAC5D;AACA;IACI,mBAAmB;AACvB;AACA;IACI,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;AACrB;;;AAGA;IACI,eAAe;AACnB;AACA;IACI,iBAAiB;IACjB,YAAY;IACZ,iBAAiB;AACrB;AACA;IACI,eAAe;AACnB;AACA;IACI,YAAY;AAChB;AACA;IACI,UAAU;IACV,YAAY;AAChB;;AAEA;IACI,UAAU;IACV,iBAAiB;IACjB,2BAA2B;AAC/B;;AAEA;IACI,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,UAAU;IACV,YAAY;AAChB;AACA;IACI,aAAa;AACjB;;AAEA;IACI,oBAAoB;IACpB,YAAY;IACZ,eAAe;IACf,aAAa;AACjB;AACA;IACI,cAAc;AAClB;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,YAAY;IACZ,cAAc;IACd,WAAW;IACX,sBAAsB;IACtB,aAAa;IACb,uCAAuC;IACvC,sBAAsB;AAC1B;;AAEA;IACI,gBAAgB;IAChB,sBAAsB;AAC1B;AACA;IACI,mBAAmB;IACnB,eAAe;AACnB","sourcesContent":["html, body, #root {\n    height: 100%;\n}\n\nbody {\n    font-family: sans-serif;\n    color: #666;\n    margin: 0;\n}\n\n#root {\n    display: flex;\n    flex-direction: column;\n}\n#root > div.graph {\n    flex: 1;\n    overflow: auto;\n    position: relative;\n}\n\n.toolbar {\n    flex-grow: 0;\n    padding: 10px 20px;\n    background: ghostwhite;\n    display: flex;\n    justify-content: space-between;\n}\n.toolbar * {\n    vertical-align: text-bottom;\n}\n\nbutton {\n    border: none;\n    background: linear-gradient(to bottom, #abb8db, #8f9fc9);\n    border-radius: 3px;\n    padding: 3px 7px;\n    color: #fff;\n    outline: none;\n    box-shadow: 2px 2px 2px rgba(0, 0, 0, .2);\n    margin-right: 5px;\n}\nbutton.grp {\n    margin-right: 15px;\n}\nbutton:active {\n    background: #6a7db0;\n}\nbutton.action {\n    background: li